.navbar .correct {
  -webkit-animation: .3s correct;
          animation: .3s correct;
}

.navbar .incorrect {
  -webkit-animation: .3s incorrect;
          animation: .3s incorrect;
}

@-webkit-keyframes correct {
  0% {
    color: white;
  }
 100% {
    color: blue;

  }
}

@keyframes correct {
  0% {
    color: white;
  }
 100% {
    color: blue;

  }
}

@-webkit-keyframes incorrect {
  0% {
    color: white;
  }

  100% {
    color:red;
  }
}

@keyframes incorrect {
  0% {
    color: white;
  }

  100% {
    color:red;
  }
}



